html {
  box-sizing: border-box;
  
}

*, ::before, ::after {
  box-sizing: inherit;
}

.body {
  font-family: 'Montserrat', sans-serif;
  background-color: gainsboro;
  
}
.form {
    display: grid;
    grid-template-columns: 1fr auto;
    grid-gap: 20px;

}

@media(max-width: 767px) {
    .form {
        grid-template-columns: 1fr;
    }
}

.form__input {

    padding: 10px;
    border: 2px solid #ddd;
}

.form__button {
    padding: 10px;
    background: teal;
    color: white;
    border: none;
    transition: 0.5s;
    cursor: pointer;
}

.form__button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.tasks {
    list-style: none;
    margin: 0;
    padding: 0;
}

.tasks__item {
    display: grid;
    grid-template-columns: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #ddd;
}

.tasks__content--done {
    text-decoration: line-through;
}

.tasks__button {
    border: none;
    color: white;
    width: 30px;
    height: 30px;
    padding: 0;
    transition: background 0.5s;
    cursor: pointer;
}

.tasks__button--toggleDone {
    background: green;
    -webkit-transform: 1s;
            transform: 1s;
}

.tasks__button--toggleDone:hover {
    background: rgb(3, 90, 3);
}

.tasks__button--toggleDone:active {
    background: rgb(2, 158, 2);
}

.tasks__button--remove {
    background: red;
    -webkit-transform: 1s;
            transform: 1s;
}

.tasks__button--remove:hover {
    background: rgb(185, 8, 8);

}

.tasks__button--remove:active {
    background: rgb(201, 69, 69);
}

.tasks__item--hidden {
    display: none;
}
.buttons {
    display: flex;
    flex-wrap: wrap;
    size: 20px;
    
}

.buttons__button {
    background: transparent;
    color: teal;
    border: none;
    margin: 0 0 0 20px;
    transition: color 0.3s;
    cursor: pointer;
    
}

@media(max-width:767px) {
    .buttons__button {
        flex-basis: 100%;
        margin: 10px;
    }
}



.buttons__button:hover {
    color: rgb(6, 173, 173);
}
.buttons__button:disabled {
    color: #ccc;
    cursor: not-allowed;
}
.section {
    margin: 10px 0;
    background: white;
    box-shadow: 0 0 5px #ddd;
    word-break: break-all;
    
}

.section__title {
    font-size: 20px;
    margin: 0;
    padding:20px;

}

.section__body {
    padding: 25px;
    border-top: 1px solid #ddd;
}

.section__header {

    border-bottom: 2px solid #ddd;
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 20px;
    justify-content: space-between;
    align-items: center;
    
}
.container {
    max-width: 900px;
    padding: 25px;
    margin: 0 auto;
}

